import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';

@Injectable()
export class FileSelectService {
  public selectOne: any;
  public selectMany: any[] = [];
  public selectMode: boolean = false;
  public singleMode: boolean = false;
  public returnPath: string;
  public returnMode: boolean = false;

  constructor(
    private _router: Router,
    private _location: Location
  ) {}

  toggle(): void {
    this.selectMode = !this.selectMode;
  }

  start(): void {
    this.selectMode = true;
  }

  stop(): void {
    this.selectMode = false;
  }

  single(): void {
    this.singleMode = true;
  }

  get(): any {
    let result: any = [];

    if(this.singleMode) {
      result = this.selectOne;
    } else {
      result = this.selectMany;
    }

    this.clear();
    return (Array.isArray(result) && result.length > 0) || (!Array.isArray(result) && typeof result === 'object') ? result : false;
  }

  add(item): void {
    if(this.singleMode) {
      item.check = true;
      if(this.selectOne && Object.keys(this.selectOne).indexOf('check') != -1) {
        delete this.selectOne.check;
      }
      this.selectOne = item;
      this.selectMany = [item];
    } else {
      if(item.check) {
        delete item.check;
        this.delete(item);
      } else {
        item.check = true;
        this.selectOne = item;
        this.selectMany.push(item);
      }
    }
  }

  check(item): boolean {
    return this.selectMany.find((foundItem) => {
      if(foundItem._id == item._id) return true;
    }) ? true : false;
  }

  replace(item): boolean {
    return this.selectMany.find((foundItem, index) => {
      if(foundItem._id == item._id) {
        item.check = true;
        this.selectOne = item;
        this.selectMany[index] = item;
        return true;
      }
    }) ? true : false;
  }

  clear(): void {
    this.selectOne = null;
    this.selectMany = [];
    this.selectMode = false;
    this.singleMode = false;
    this.returnMode = false;
  }

  delete(item): void {
    let self = this;
    if(this.selectOne == item) {
      this.selectOne = null;
    };

    this.selectMany
      .find((file, index) => {
        if(item._id == file._id) {
          
          self.selectMany.splice(index, 1);
          return true;
        }
      });
  }

  returnRoute(): void {
    if(this.returnPath) {
      this._router.navigate([this.returnPath]);
    } else {
      this._location.back();
    }
  }

}



