import { Injectable } from '@angular/core';

@Injectable()
export class FormDataService {
  private formData: any;

  constructor() {}

  set(data): void {
    this.formData = data;
  }

  get(): any {
    return this.formData;
  }

  clear(): void {
      this.formData = null;
  }

  check(): boolean {
      return this.formData ? true : false;
  }

}



